          class="relative mx-auto border-gray-800 dark:border-gray-800 bg-gray-800 border-[14px] rounded-[2.5rem] h-[600px] w-[300px]"
        >
          <div
            class="h-[32px] w-[3px] bg-gray-800 dark:bg-gray-800 absolute -start-[17px] top-[72px] rounded-s-lg"
          ></div>
          <div
            class="h-[46px] w-[3px] bg-gray-800 dark:bg-gray-800 absolute -start-[17px] top-[124px] rounded-s-lg"
          ></div>
          <div
            class="h-[46px] w-[3px] bg-gray-800 dark:bg-gray-800 absolute -start-[17px] top-[178px] rounded-s-lg"
          ></div>
          <div
            class="h-[64px] w-[3px] bg-gray-800 dark:bg-gray-800 absolute -end-[17px] top-[142px] rounded-e-lg"
          ></div>
          <div
            class="rounded-[2rem] overflow-hidden w-[272px] h-[572px] bg-white dark:bg-gray-800"
          >
            <img
              :src="COMMON.devices.android.ligth"
              class="dark:hidden w-[272px] h-[572px]"
              alt=""
            />
            <img
              :src="COMMON.devices.android.dark"
              class="hidden dark:block w-[272px] h-[572px]"
              alt=""
            />
          </div>
        </div>
      </div>
      <div v-show="android != null" class="app-item">
        <div
          style="
            background-image: url('https://wework.qpic.cn/wwpic3az/158093_UEtnYGicSm6rdUu_1707298593/0');
          "
          class="w-full h-full text-center rounded-lg py-8 lg:py-16 px-4 bg-center bg-no-repeat dark:bg-gray-700 bg-gray-700 bg-blend-multiply"
        >
          <div
            class="mb-8 text-xl font-bold lg:text-2xl sm:px-0 lg:px-16 text-white"
          >
            {{ android.name }}
          </div>
          <div
            class="mb-2 text-lg font-normal lg:text-xl sm:px-0 lg:px-16 text-gray-300 dark:text-gray-300"
          >
            轻便追剧，畅享高清乐趣!
          </div>
          <fwb-button
            @click="downApp(android.down_url)"
            gradient="blue"
            size="lg"
            class="rounded-sm"
          >
            <template #prefix>
              <svg
                class="w-6 h-6 text-white"
                aria-hidden="true"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
              >
                <path
                  stroke="currentColor"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M4 15v2a3 3 0 0 0 3 3h10a3 3 0 0 0 3-3v-2m-8 1V4m0 12-4-4m4 4 4-4"
                />
              </svg>
            </template>
            下载安装
          </fwb-button>
          <div
            class="mt-2 text-sm font-normal lg:text-xl sm:px-0 lg:px-16 text-gray-300 dark:text-gray-300"
          >
            最新版本:{{ android.version }}
          </div>
          <div
            v-show="android.content.split('\n').length > 0"
            class="app-apdate-logs"
          >
            <div
              class="mt-2 text-left text-sm font-normal lg:text-xl sm:px-0 lg:px-16 text-gray-300 dark:text-gray-300"
            >
              更新日志:
            </div>
            <div
              v-for="(item, index) in android.content.split('\n')"
              v-bind:key="index"
              class="mt-2 text-left text-sm font-normal lg:text-xl sm:px-0 lg:px-16 text-gray-300 dark:text-gray-300"
            >
              {{ item }}
            </div>
          </div>
        </div>
      </div>
      <div v-show="windows != null" class="app-item">
        <div
          class="relative mx-auto border-gray-800 dark:border-gray-800 bg-gray-800 border-[8px] rounded-t-xl h-[172px] max-w-[301px] md:h-[294px] md:max-w-[512px]"
        >
          <div
            class="rounded-lg overflow-hidden h-[156px] md:h-[278px] bg-white dark:bg-gray-800"
          >
            <img
              :src="COMMON.devices.windows.ligth"
              class="dark:hidden h-[156px] md:h-[278px] w-full rounded-xl"
              alt=""
            />
            <img
              :src="COMMON.devices.windows.dark"
              class="hidden dark:block h-[156px] md:h-[278px] w-full rounded-lg"
              alt=""
            />
          </div>
        </div>
        <div
          class="relative mx-auto bg-gray-900 dark:bg-gray-700 rounded-b-xl rounded-t-sm h-[17px] max-w-[351px] md:h-[21px] md:max-w-[597px]"
        >
          <div
            class="absolute left-1/2 top-0 -translate-x-1/2 rounded-b-xl w-[56px] h-[5px] md:w-[96px] md:h-[8px] bg-gray-800"
          ></div>
        </div>
      </div>
      <div v-show="windows != null" class="app-item">
        <div
          style="
            background-image: url('https://wework.qpic.cn/wwpic3az/386774_7-2SfaOBQ0y9f3Z_1707298563/0');
          "
          class="w-full h-full text-center rounded-lg py-8 lg:py-16 px-4 bg-center bg-no-repeat dark:bg-gray-700 bg-gray-700 bg-blend-multiply"
        >
          <div
            class="mb-8 text-xl font-bold lg:text-2xl sm:px-0 lg:px-16 text-white"
          >
            {{ windows.name }}
          </div>
          <div
            class="mb-2 text-lg font-normal lg:text-xl sm:px-0 lg:px-16 text-gray-300 dark:text-gray-300"
          >
            畅享高清追剧乐趣!
          </div>
          <fwb-button
            @click="downApp(windows.down_url)"
            gradient="blue"
            size="lg"
            class="rounded-sm"
          >
            <template #prefix>
              <svg
                class="w-6 h-6 text-white"
                aria-hidden="true"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
              >
                <path
                  stroke="currentColor"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M4 15v2a3 3 0 0 0 3 3h10a3 3 0 0 0 3-3v-2m-8 1V4m0 12-4-4m4 4 4-4"
                />
              </svg>
            </template>
            下载安装
          </fwb-button>
          <div
            class="mt-2 text-sm font-normal lg:text-xl sm:px-0 lg:px-16 text-gray-300 dark:text-gray-300"
          >
            最新版本:{{ windows.version }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useToast, FwbButton } from "flowbite-vue";
import { ref, onMounted, getCurrentInstance } from "vue";
import LoadSkeleton from "../widgets/LoadSkeleton.vue";

export default {
  components: {
    LoadSkeleton,
    FwbButton,
  },
  setup() {
    const loading = ref(true);
    const toast = useToast();
    const android = ref(null);
    const windows = ref(null);
    const error = ref(null);
    const { proxy } = getCurrentInstance();

    function fetchData() {
      let api = `${proxy.COMMON.apiUrl}/v1/api/app/search?q=android`;
      fetch(api, {
        method: "post",
        headers: {
          "content-type": "application/json",
          Authorization: localStorage.getItem("Authorization"),
        },
      })
        .then((res) => {
          if (!res.ok) {
            const error = new Error(res.statusText);
            error.json = res.json();
            throw error;
          }
          return res.json();
        })
        .then((json) => {
          if (json.code == 200) {
            if (json.data.length === 1) {
              android.value = json.data[0];
              fetchWinData();
            } else {
              toast.add({
                type: "success",
                time: 3000,
                text: "无客户端可供下载!",
              });
            }
          } else if (json.code == 402) {
            toast.add({
              type: "success",
              time: 3000,
              text: "刷新token成功!",
            });
            localStorage.setItem("Authorization", json.data);
          } else if (json.code == 403) {
            toast.add({
              type: "warning",
              time: 3000,
              text: "登录失效,请重新登录!",
            });
            proxy.COMMON.loginOut();
          } else {
            toast.add({
              type: "success",
              time: 3000,
              text: json.msg,
            });
          }
        })
        .catch((err) => {
          error.value = err;
          if (err.json) {
            return err.json.then((json) => {
              error.value.message = json.message;
              toast.add({
                type: "success",
                time: 3000,
                text: error.value.message,
              });
            });
          }
        });
    }

    function fetchWinData() {
      let api = `${proxy.COMMON.apiUrl}/v1/api/app/search?q=windows`;
      fetch(api, {
        method: "post",
        headers: {
          "content-type": "application/json",
          Authorization: localStorage.getItem("Authorization"),
        },
      })
        .then((res) => {
          if (!res.ok) {
            const error = new Error(res.statusText);
            error.json = res.json();
            throw error;
          }
          return res.json();
        })
        .then((json) => {
          if (json.code == 200) {
            if (json.data.length === 1) {
              windows.value = json.data[0];
            }
          } else if (json.code == 402) {
            toast.add({
              type: "success",
              time: 3000,
              text: "刷新token成功!",
            });
            localStorage.setItem("Authorization", json.data);
          } else if (json.code == 403) {
            toast.add({
              type: "warning",
              time: 3000,
              text: "登录失效,请重新登录!",
            });
            proxy.COMMON.loginOut();
          } else {
            toast.add({
              type: "success",
              time: 3000,
              text: json.msg,
            });
          }
        })
        .catch((err) => {
          error.value = err;
          if (err.json) {
            return err.json.then((json) => {
              error.value.message = json.message;
              toast.add({
                type: "success",
                time: 3000,
                text: error.value.message,
              });
            });
          }
        })
        .then(() => {
          loading.value = false;
        });
    }
    onMounted(() => {
      document.title = "应用";
      fetchData();
    });
    return {
      loading,
      toast,
      android,
      windows,
    };
  },
  methods: {
    downApp(url) {
      window.open(url);
    },
  },
};
</script>

